
.font-size-item{
  float:left;
  width:56px;
  line-height:32px;
  text-align: center;
  border-top: 1px solid #eee;
  border-left: 1px solid #eee;
  cursor: pointer;
}
.font-size-item:hover, .font-size-wrapper .selected{
  background-color: #eee;
}
.font-size-wrapper{
  width:168px;
}
.font-size-wrapper div:nth-child(-n+3){
  border-top:0;
}
.font-size-wrapper div:nth-child(3n+1){
  border-left:0;
}




