
[contenteditable] {
  -webkit-user-select: text;
     -moz-user-select: text;
      -ms-user-select: text;
          user-select: text;
}
[contenteditable="false"] {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.element-text-content {
  outline:none
}
.element-text-content *::-moz-selection {
  background: #337ab7;
  color: #fff;
}
.element-text-content *::selection {
  background: #337ab7;
  color: #fff;
}
.element-text-content p{
  margin:0;
}
.form-createlinks .btn-group {
  box-shadow: none;
}
.form-createlinks input {
  height: 33px;
  border-radius: 4px 0 0 4px;
  border-right: none !important;
}
.form-createlinks button {
  border-radius: 0;
}
.form-createlinks button:last-child {
  border-radius: 0 4px 4px 0;
}
.form-createlinks input:focus {
  outline: none;
  box-shadow: none;
  border: 1px solid #ccc;
}
.btn.unlink {
  background-color: #eee;
}
.glyphicon.unlink {
  text-decoration: line-through;
}



