
.modal-mask{position:fixed;z-index:2000;top:0;left:0;right:0;bottom:0;background-color:rgba(0,0,0,0.6)
}
.modal-wrapper{height:100%;text-align:center;overflow-y:auto
}
.modal-wrapper::after{display:inline-block;content:'';height:100%;vertical-align:middle
}
.modal-container{position:relative;display:inline-block;margin:20px auto;background-color:#fff;border-radius:6px;overflow:hidden;vertical-align:middle;box-shadow:0 2px 8px rgba(0,0,0,0.33)
}
.modal-header{height:60px;padding:12px;background:#f6f6f6;border-bottom:1px solid #eee
}
.modal-body{height:calc(100% - 114px);padding:12px;background:#fff;border-bottom:1px solid #eee;overflow:auto;position:relative
}
.modal-footer{padding:12px;height:54px;text-align:right
}



