
.element-html-wrapper {
  width: 100%;
  height: 100%;
  border: 1px solid #ccc;
  background: #eee;
  display: table;
}
.element-html-title {
  font-size: 20px;
  color: #91aa9d;
}
.element-html-text {
  text-align: center;
  display: table-cell;
  vertical-align: middle;
}
.element-html-input {
  height: 100%;
}



