
.pc-iframe{
  position: absolute;
  top:45px;
  bottom:0;
  width:100%;
  height:calc(100% - 45px);
}
.mobile-preview{
  position: relative;
  top:10px;
  width: 100%;
  text-align:center;
}
.mobile-iframe{
  overflow-x: hidden;
  overflow-y: scroll;
  z-index: 3;
  display: block;
  border: none;
  height: 667px;
  width: 375px;
  position: absolute;
  margin: auto;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}



