
.avatar {
  display: inline-block;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  overflow: hidden;
  margin-left: 6px;
  line-height: 34px;
  font-size: 12px;
  text-align: center;
  color: #666;
  cursor: pointer;
  box-shadow: 0 0 8px #ddd;
}
.avatar:hover {
  border-color: #bbb;
}
.avatar.invited {
  border-style: dashed;
  background: #f9f9f9;
}
