
.dropup-enter-active, .dropup-leave-active,
.dropdown-enter-active, .dropdown-leave-active {
  transition: all .3s ease;
  overflow: hidden;
}
.dropdown-enter, .dropdown-leave-active {
  transform: translateY(-20px);
  opacity: 0;
}
.dropup-enter, .dropup-leave-active {
  transform: translateY(20px);
  opacity: 0;
}



